.price {
  background-color: #e5edf1;

  .wrapper {
    padding: 50px 25px 46px;
  }
}

@media (min-width: @tablet-width) {
  .price {
    .wrapper {
      padding: 50px;
    }
  }
}

@media (min-width: @desktop-width) {
  .price {
    .wrapper {
      padding: 70px;
      max-width: 1440px;
      margin: 0 auto;
    }
  }
}

.price__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  color: #203356;
  margin: 0;
  margin-bottom: 5px;
}

@media (min-width: @tablet-width) {
  .price__title {
    font-size: 36px;
  }
}

@media (min-width: @desktop-width) {
  .price__title {
    margin-bottom: 40px;
  }
}

.price__information {
  display: flex;
  align-items: center;
  font-weight: 400;
  line-height: 101.4%;
  width: 100%;
  padding-bottom: 11px;
  padding-top: 28px;
  border-bottom: 1px solid #9ba3b6;
}

@media (min-width: @desktop-width) {
  .price__information {
    align-items: baseline;
  }
}

.price__subtitle {
  margin: 0;
  text-transform: uppercase;
  font-weight: 600;
  width: 190px;
  font-size: 14px;
}

@media (min-width: @tablet-width) {
  .price__subtitle {
    font-size: 16px;
    width: 220px;
  }
}

@media (min-width: @big-tablet-width) {
  .price__subtitle {
    font-size: 24px;
    margin-top: 12px;
    margin-bottom: 25px;
    width: 330px;
    line-height: 104%;
  }
}

@media (min-width: @desktop-width) {
  .price__subtitle {
    width: 550px;
    line-height: 100%;
  }
}

.price__unit {
  margin: 0;
  margin-left: auto;
  text-align: left;
}

@media (min-width: @big-tablet-width) {
  .price__unit {
    font-size: 22px;
    min-width: 40px;
    text-align: left;
  }
}


.price__from {
  margin-left: auto;
  min-width: 58px;
  text-align: right;
}

@media (min-width: @big-tablet-width) {
  .price__from {
    margin-left: 152px;
    font-size: 22px;
    min-width: 88px;
  }
}

.price__coruns {
  display: none;
}

@media (min-width: @tablet-width) {
  .price__coruns {
    display: block;
    margin-left: auto;
  }
}

@media (min-width: @big-tablet-width) {
  .price__coruns {
    margin-left: 92px;
    font-size: 22px;
  }
}

.price__wrapper {
  margin-bottom: 26px;
}

@media (min-width: @desktop-width) {
  .price__wrapper {
    margin-bottom: 64px;
  }
}

.price__desc {
  font-size: 12px;
  text-align: right;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .price__desc {
    font-size: 14px;
  }
}

@media (min-width: @desktop-width) {
  .price__desc {
    font-size: 18px;
  }
}
