.service {
  background-color: #e5edf1;

  .wrapper {
    padding: 50px 25px;
  }
}

@media (min-width: @tablet-width) {
  .service {
    .wrapper {
      padding: 50px;
    }
  }
}

@media (min-width: @desktop-width) {
  .service {
    .wrapper {
      padding: 69px 70px;
      max-width: 1440px;
      margin: 0 auto;
    }
  }
}

.service__wrapper {
  position: relative;
}

.service__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  color: #203356;
  margin: 0;
  margin-bottom: 44px;
}

@media (min-width: @tablet-width) {
  .service__title {
    font-size: 36px;
    margin-bottom: 54px;
  }
}

.service__subtitle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  color: #f1faff;
  margin: 0;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .service__subtitle {
    font-size: 24px;
  }
}

@media (min-width: @big-tablet-width) {
  .service__subtitle {
    font-size: 21px;
  }
}

@media (min-width: @desktop-width) {
  .service__subtitle {
    font-size: 24px;
  }
}

.service__image {
  display: block;
  margin-bottom: 25px;
  width: 100%;
  border-radius: 5px;
  box-shadow: 2px 4px 4px 0 rgba(0, 0, 0, 0.25);
}

@media (min-width: @tablet-width) {
  .service__image {
    margin-bottom: 40px;
  }
}

@media (min-width: @desktop-width) {
  .service__image {
    height: 269px;
    object-fit: fill;
    margin-bottom: 39px;
  }
}

.service__desc {
  margin: 0;
}

@media (min-width: @desktop-width) {
  .service__desc {
    width: 358px;
  }
}

.service__item {
  margin-bottom: 51px;
}

@media (min-width: @tablet-width) {
  .service__item {
    margin-bottom: 40px;
  }
}

@media (min-width: @big-tablet-width) {
  .service__item {
    width: 32%;
    margin-bottom: 0;
  }
}


@media (min-width: @desktop-width) {
  .service__item {
    width: 100%;
  }
}

.service__list .service__item:last-child {
  margin-bottom: 0;
}

@media (min-width: @big-tablet-width) {
  .service__list {
    display: flex;
    justify-content: space-between;
    gap: 25px;
  }
}

@media (min-width: @desktop-width) {
  .service__list {
    display: flex;
    justify-content: space-between;
    gap: 42px;
  }
}
