.form {
  background-color: #203356;

  .wrapper {
    padding: 40px 25px 60px;
  }

  .button {
    background-color: transparent;

    &:hover,
    &:focus,
    &:active {
      background-color: #f1faff;
      color: #10273b;
      border: none;
    }
  }
}

@media (min-width: @tablet-width) {
  .form {
    .wrapper {
      padding: 50px;
    }

    .button {
      margin: 0 auto;
    }
  }
}

@media (min-width: @desktop-width) {
  .form {
    .wrapper {
      padding: 42px 70px 70px;
      max-width: 1440px;
      margin: 0 auto;
    }
  }
}

.form__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  color: #f1faff;
  margin: 0;
  padding: 50px 25px 0;
}

@media (min-width: @tablet-width) {
  .form__title {
    font-size: 36px;
    padding: 50px 50px 0;
  }
}

@media (min-width: @desktop-width) {
  .form__title {
    padding: 70px 70px 0;
    margin: 0 auto;
    max-width: 1440px;
  }
}

.form__button::after {
  content: "";
  display: none;
  width: 35px;
  height: 35px;
  background-image: url("../../img/five.svg");
  background-repeat: no-repeat;
  background-size: 35px 27px;
  margin-left: 18px;
}

.form__button.sent {
  border: 3px solid transparent;
  background-color: #203356;


  &:hover,
  &:focus,
  &:active {
    background-color: transparent;
    color: #f1faff;
    border: 3px solid transparent;
  }
}

.form__button.sent::after {
  display: inline-block;
}
