.about {
  padding: 50px 25px 50px;
}

@media (min-width: @tablet-width) {
  .about {
    padding: 50px;

    .button {
      margin: 0 auto;
    }
  }
}

@media (min-width: @big-tablet-width) {
  .about {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;

    .button {
      margin: 0;
      width: 100%;
      max-width: 400px;
      max-height: 74px;

    }
  }
}

@media (min-width: @desktop-width) {
  .about {
    padding: 70px;
    max-width: 1440px;
    grid-template-columns: 1fr 521px;
    margin: 0 auto;
    column-gap: 77px;

    .button {
      margin: 0;
    }
  }
}

.about__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  margin: 0;
  margin-bottom: 27px;
}

@media (min-width: @tablet-width) {
  .about__title {
    font-size: 36px;
  }
}

@media (min-width: @big-tablet-width) {
  .about__title {
    grid-row: 1/2;
    font-size: 36px;
  }
}

@media (min-width: @desktop-width) {
  .about__title {
    margin-bottom: 40px;
  }
}

.about__desc {
  margin: 0;
  margin-bottom: 20px;
}

@media (min-width: @big-tablet-width) {
  .about__desc {
    grid-row: 2/3;
    margin-bottom: 21px;
  }
}

.about__desc--second {
  margin-bottom: 28px;
}

@media (min-width: @big-tablet-width) {
  .about__desc--second {
    grid-row: 3/4;
    margin-bottom: 21px;
  }
}

@media (min-width: @desktop-width) {
  .about__desc--second {
    margin-bottom: 105px;
  }
}

.about__image {
  margin-bottom: 34px;
  display: block;
  width: 100%;
  border-radius: 5px;
  box-shadow: 2px 4px 4px 0 rgba(0, 0, 0, 0.25);
}

@media (min-width: @big-tablet-width) {
  .about__image {
    grid-row: 1/5;
    margin-bottom: 0;
    max-width: 521px;
  }
}

@media (min-width: @desktop-width) {
  .about__image {
    height: 369px;
  }
}

