.main-header__contacts {
  background-color: #e5edf1;
  border-bottom: 1px solid #29609c;
  min-height: 65px;

  .wrapper {
    padding: 11px 25px;
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: @tablet-width) {
  .main-header__contacts {
    min-height: 60px;

    .wrapper {
      padding: 15px 50px;
      max-width: 1440px;
      margin: 0 auto;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-header__contacts {
    .wrapper {
      padding: 15px 70px;
    }
  }
}

.main-header__link {
  color: #10273b;
  font-size: 14px;
  display: flex;
  align-items: center;

  &:hover,
  &:focus {
    color: #66b2f9;
    outline: none;
    text-decoration: underline;
  }
}

@media (min-width: @tablet-width) {
  .main-header__link {
    font-size: 18px;
  }
}

.main-header__email {
  letter-spacing: 1px;
}

.main-header__icon,
.main-header__social-icon {
  color: #10273b;
}


@media (min-width: @tablet-width) {
  .main-header__icon {
    width: 30px;
    height: 30px;
  }
}

@media (min-width: @tablet-width) {
  .main-header__icon--email {
    height: 25px;
  }
}

@media (min-width: @tablet-width) {
  .main-header__social-icon {
    width: 30px;
    height: 30px;
  }
}

.main-header__social-list {
  display: flex;
  gap: 30px;
  align-items: center;
  padding-top: 4px;
}

@media (min-width: @tablet-width) {
  .main-header__social-list {
    gap: 23px;
    padding-top: 0;
  }
}

.main-header__item {
  display: flex;
  align-items: center;
}

@media (min-width: @tablet-width) {
  .main-header__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    flex-direction: row-reverse;
    gap: 23px;
  }
}

@media (min-width: @desktop-width) {
  .main-header__list {
    gap: 68px;
    width: 600px;
  }
}

.main-header__social-link {
  display: inline-block;
  color: #10273b;

  &:hover path,
  &:focus path {
    fill: #66b2f9;
  }

  &:focus {
    outline: none;
  }
}


.main-header__social-item {
  height: 20px;
}

@media (min-width: @tablet-width) {
  .main-header__social-item {
    height: 30px;
  }
}

.main-header__text {
  margin-left: 8px;
}

@media (min-width: @tablet-width) {
  .main-header__text {
    margin-left: 14px;
  }
}
