.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  font-style: normal;
  line-height: 101.4%;
  text-transform: uppercase;
  color: #f1faff;
  background-color: rgba(43, 99, 162, 0.5);
  border: 3px solid #cbdee8;
  border-radius: 5px;
  padding: 19px 16px 14px;
  min-height: 60px;
  width: 100%;
  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
    background-color: rgba(241, 250, 255, 0.1);
  }

  &:active {
    background-color: #f1faff;
    color: #2b63a2;
    border: 3px solid #f1faff;
  }
}

@media (min-width: @tablet-width) {
  .button {
    width: 400px;
  }
}

@media (min-width: @desktop-width) {
  .button {
    padding: 22px 16px;
    font-size: 24px;
  }
}

.button--secondary {
  background-color: #10273b;
  color: #e3f5fe;
  border: none;
  padding: 17px 21px 16px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);

  &:hover,
  &:focus,
  &:active {
    background-color: #66b2f9;
    color: #10273b;
    border: none;
  }
}

@media (min-width: @desktop-width) {
  .button--secondary {
    padding: 22px 16px;
    font-size: 24px;
    min-height: 74px;
  }
}
