html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  height: 100%;
  min-width: 320px;
  color: #10273b;
  background-color: #f1faff;
  font-family: "Figtree", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
}

@media (min-width: @tablet-width) {
  body {
    font-size: 18px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}
