.main-footer {
  background-color: #f1faff;
  padding: 50px 75px 70px;
}

@media (min-width: @tablet-width) {
  .main-footer {
    display: flex;
    padding: 50px;
  }
}

@media (min-width: @desktop-width) {
  .main-footer {
    padding: 50px 70px 55px;
    max-width: 1440px;
    margin: 0 auto;
  }
}

.main-footer__logo {
  color: #10273b;
  width: 208px;
  display: block;
  margin: 0 auto;
  margin-bottom: 33px;

  &:hover path,
  &:focus path {
    fill: #66b2f9;
    outline: none;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__logo {
    margin: 0;
    margin-bottom: 41px;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__logo {
    margin-bottom: 36px;
  }
}

.main-footer__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  color: #203356;
  margin: 0;
  margin-bottom: 46px;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .main-footer__title {
    margin-bottom: 45px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__title {
    font-size: 36px;
    margin-bottom: 35px;
  }
}

.main-footer__desc {
  font-weight: 400;
  line-height: 130%;
  margin: 0;
  margin-bottom: 28px;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .main-footer__desc {
    text-align: left;
    font-size: 18px;
    width: 280px;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__desc {
    margin-bottom: 64px;
  }
}

.main-footer__social-list {
  display: flex;
  justify-content: center;
  gap: 34px;
  margin-bottom: 64px;
}

@media (min-width: @tablet-width) {
  .main-footer__social-list {
    margin-bottom: 0;
  }
}

.main-footer__social-icon {
  color: #203356;
}

@media (min-width: @tablet-width) {
  .main-footer__social-icon {
    width: 30px;
    height: 30px;
  }
}

.main-footer__contact-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  min-width: 167px;
}

@media (min-width: @tablet-width) {
  .main-footer__contact-item {
    margin-bottom: 25px;
  }
}

.main-footer__contact-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 30px 67px 32px;
  font-weight: 400;

  .main-footer__contact-item:last-child {
    margin-bottom: 0;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__contact-list {
    margin: 0;
    align-items: flex-start;
    font-size: 18px;
  }
}


.main-footer__link,
.main-footer__adress,
.main-footer__contact-link {
  margin-left: 7px;
  display: inline-block;
  color: #10273b;
}

.main-footer__contact-link,
.main-footer__link {
  &:hover,
  &:focus {
    color: #66b2f9;
    outline: none;
    text-decoration: underline;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__contact-link,
  .main-footer__adress {
    margin-left: 14px;
  }
}

.main-footer__link {
  margin-left: 2px;
}

@media (min-width: @desktop-width) {
  .main-footer__link {
    margin-left: 8px;
  }
}

.main-footer__item {
  margin-bottom: 25px;
  position: relative;
  min-width: 66px;
}

@media (min-width: @desktop-width) {
  .main-footer__item {
    min-width: 82px;
    margin-bottom: 23px;
  }
}

.main-footer__list {
  margin: 0 auto;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  align-items: center;

  .main-footer__item:last-child {
    margin-bottom: 0;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__list {
    font-size: 16px;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__list {
    font-size: 18px;
  }
}

.main-footer__links {
  .main-footer__title {
    margin-bottom: 32px;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__links {
    margin-left: auto;

    .main-footer__title {
      margin-bottom: 43px;
      order: 2;
    }
  }
}

@media (min-width: @tablet-width) {
  .main-footer__links {
    .main-footer__title {
      margin-bottom: 38px;
    }
  }
}

@media (min-width: @tablet-width) {
  .main-footer__about {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (min-width: @tablet-width) {
  .main-footer__contact {
    order: 3;
    margin-left: auto;

    .main-footer__title {
      margin-bottom: 41px;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-footer__icon {
    width: 30px;
    height: 30px;
  }
}

@media (min-width: @desktop-width) {
  .main-footer__icon--email {
    width: 30px;
    height: 26px;
  }
}

.main-footer__social-link {
  &:hover path,
  &:focus path {
    fill: #66b2f9;
    outline: none;
  }
}
