.hero {
  background-color: #29609c;
  background-image: url("../img/mobile-hero-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  .wrapper {
    padding: 219px 25px 70px;
  }
}

@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/mobile-hero-background@2x.jpg");
  }
}

@media (min-width: @tablet-width) {
  .hero {
    background-image: url("../img/desktop-hero-background.jpg");
    background-size: cover;

    .wrapper {
      padding: 170px 50px 70px;
      max-width: 1440px;
      margin: 0 auto;
    }

    .button {
      text-align: center;
      justify-content: flex-start;
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: 122px 22px;
      padding-left: 163px;

      &:active {
        background-image: url("../img/arrow-active.svg");
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .hero {
    .wrapper {
      padding: 150px 70px 150px;
    }

    .button {
      background-position: 92px 27px;
      padding-left: 160px;
    }
  }
}

@media (min-width: @tablet-width) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/desktop-hero-background@2x.jpg");
  }
}

.hero__title,
.hero__word {
  color: #f1faff;
  margin: 0;
  font-size: 30px;
  font-weight: 700;
  line-height: 101.4%;
  text-transform: uppercase;
  text-align: center;
}

.hero__title {
  margin-bottom: 8px;
}

@media (min-width: @tablet-width) {
  .hero__title {
    font-size: 48px;
    margin-bottom: 8px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .hero__title {
    font-size: 76px;
    margin-bottom: 7px;
  }
}

.hero__word {
  font-size: 32px;
  margin-bottom: 29px;
  font-weight: 800;
  word-wrap: break-word;
}

@media (min-width: @tablet-width) {
  .hero__word {
    font-size: 60px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .hero__word {
    font-size: 88px;
    margin-bottom: 40px;
  }
}

.hero__desc {
  color: #f1faff;
  font-size: 16px;
  margin: 0;
  margin-bottom: 138px;
  line-height: 101.4%;
  text-align: center;
}

@media (min-width: @tablet-width) {
  .hero__desc {
    font-size: 20px;
    text-align: left;
  }
}

@media (min-width: @desktop-width) {
  .hero__desc {
    font-size: 36px;
    margin-bottom: 150px;
  }
}
