.gallery {
  padding: 45px 25px 45px;

  .wrapper {
    padding: 0 41px;
  }
}

@media (min-width: @tablet-width) {
  .gallery {
    padding: 50px;
  }
}

@media (min-width: @desktop-width) {
  .gallery {
    padding: 70px 70px 61px;
    max-width: 1440px;
    margin: 0 auto;

    .wrapper {
      padding: 0 50px;
    }
  }
}

.gallery__wrapper {
  position: relative;
}

@media (min-width: @tablet-width) {
  .gallery__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
}

.gallery__slider-image {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 5px;
  box-shadow: 2px 4px 4px 0 rgba(0, 0, 0, 0.25);
  margin-bottom: 25px;
}

@media (min-width: @tablet-width) {
  .gallery__slider-image {
    display: inline-block;
    width: 100%;
  }
}

@media (min-width: @desktop-width) {
  .gallery__slider-image {
    display: inline-block;
    margin-bottom: 37px;
  }
}

@media (min-width: @tablet-width) {
  .gallery__picture {
    width: 48%;
    height: 50%;
    transition: opacity 0.5s ease;
  }
}

@media (min-width: @desktop-width) {
  .gallery__picture {
    width: 33%;
    height: 33%;
  }
}

.gallery__title {
  font-size: 24px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 101.4%;
  margin: 0;
  margin-bottom: 45px;
}

@media (min-width: @tablet-width) {
  .gallery__title {
    font-size: 36px;
  }
}

@media (min-width: @desktop-width) {
  .gallery__title {
    margin-bottom: 52px;
  }
}

.gallery__slider-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  color: #10273b;

  &:hover,
  &:focus {
    outline: none;
    color: #66b2f9;
  }
}

.gallery__slider-button--prev {
  transform: rotate(180deg) translateY(50%);
  left: -48px;
}

@media (min-width: @desktop-width) {
  .gallery__slider-button--prev {
    left: -57px;
  }
}

.gallery__slider-button--next {
  right: -48px;
}

@media (min-width: @tablet-width) {
  .gallery__slider-button--next {
    right: -46px;
  }
}

@media (min-width: @desktop-width) {
  .gallery__slider-button--next {
    right: -59px;
  }
}

.gallery__slider-pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 9px;
}

.gallery__slider-pagination-button {
  width: 12px;
  height: 12px;
  margin: 0;
  padding: 0;
  background-color: #10273b;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    .gallery__slider-pagination-button:nth-child(2n) {
      background-color: #66b2f9;
      border: 1px solid #10273b;
    }
  }

  &:hover,
  &:focus,
  &:active {
    outline: none;
    background-color: #66b2f9;
  }
}

.gallery__slider-pagination-button--current {
  background-color: #66b2f9;
  border: 1px solid #10273b;
}

@media (min-width: @desktop-width) {
  .gallery__slider-icon {
    width: 22px;
    height: 51px;
  }
}
