.form-order__group {
  border: none;
  margin: 0;
  padding: 0;
}

.form-order__input {
  width: 100%;
  padding: 10px 8px 8px;
  margin-bottom: 9px;
  border-radius: 5px;
  border: 3px solid transparent;
  font-size: 14px;
  color: #21293f;
  line-height: 101.4%;
  background-color: #f1faff;

  &:focus,
  &:hover {
    outline: none;
    border: 3px solid #66b2f9;
  }

  &::placeholder {
    color: #10273b;
  }
}


@media (min-width: @big-tablet-width) {
  .form-order__input {
    margin-bottom: 40px;
    padding: 14px 18px;
    font-size: 18px;
  }
}

.form-order__label--custom {
  position: relative;
  display: inline-block;
  padding: 10px 8px;
  background-color: #f1faff;
  color: #21293f;
  cursor: pointer;
  border: 2px solid transparent;
  border-radius: 5px;
  font-family: "Figtree", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 101.4%;
  width: 100%;
  margin-bottom: 10px;

  &:hover,
  &:focus-within {
    outline: none;
    border: 3px solid #66b2f9;
  }
}

@media (min-width: @big-tablet-width) {
  .form-order__label--custom {
    margin-bottom: 0;
    padding: 15px 15px 12px;
    font-size: 18px;
    min-height: 55px;
    border: 3px solid transparent;
  }
}

.form-order__input--custom {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.form-order__comment {
  width: 100%;
  background-color: #f1faff;
  color: #21293f;
  font-family: "Figtree", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 101.4%;
  border: 3px solid transparent;
  padding: 12px 6px;
  border-radius: 5px;
  resize: none;
  min-height: 202px;

  &:focus,
  &:hover {
    outline: none;
    border: 3px solid #66b2f9;
  }
}

@media (min-width: @big-tablet-width) {
  .form-order__comment {
    min-height: 231px;
    font-size: 18px;
  }
}

.form-order__comments {
  margin-bottom: 34px;
}

@media (min-width: @big-tablet-width) {
  .form-order__comments {
    margin-bottom: 29px;
  }
}

@media (min-width: @big-tablet-width) {
  .form-order {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 74px;
    column-gap: 80px;

    .button {
      grid-row: 4/5;
      grid-column: 2/3;
      width: 100%;
    }
  }
}

@media (min-width: @big-tablet-width) {
  .form-order__group {
    grid-row: 1/5;
    grid-column: 1/2;
  }
}

@media (min-width: @big-tablet-width) {
  .form-order__group--comments {
    grid-row: 1/4;
    grid-column: 2/3;
  }
}
